.player{
    transition: all ease 0.5s;
    position: fixed;
    padding: 20px 10px;
    bottom: 0;
    height: 50px;
    width: 97.9%;
    display: flex;
    justify-content: space-between;
    padding: 10px 20px;
    align-items: center;
    color: #D9D9D9;
    font-family: inter;
    background: #1D3037;
}
.player-left{
  display: flex;
  align-items: center;
  gap: 23px;
  line-height: 8px;
  font-size: 14px;
  width: 25%;
}
.player-left #icon {
    width: 40px;
    height: 40px;
    border-radius: 6px;
    background: #D9D9D9;
    background: url("/public/images/song.png") center  #fff;
    background-repeat: no-repeat;
    background-size: 30%;
  }
  .player-left #title{
    font-family: "inter bold";
  }
  .player-center {
    width: 15%;
    display: flex;
    align-items: flex-start;
    gap: 81px;
      position: absolute;
      left: 45%;
  }
.playStop, .fast-left, .fast-right, .volume{
    cursor: pointer;
}
.loading {
    animation: spin 2s linear infinite;
    cursor: progress;
}
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }

.player-right{
    display: flex;
    align-items: center;
    gap: 14px;
    color: #1D3037;
}
.player-right .displayer{
    position: relative;
    border-radius: 6px;
}
#volume-progression {
  z-index: -1;
  position: relative;
  top: -16px;
  height: 6px;
  background-color: #D9D9D9;
  border-radius: 6px;
  user-select: none;
}
input[type="range"] {
  width: 100%;
  height: 6px; 
  margin: 10px 0;
  -webkit-appearance: none;
  cursor: pointer;
  opacity: 0.5;
}
input[type="range"]::-webkit-slider-runnable-track {
  width: 100%;
  height: 100%;
  background: #ffffff; 
  border-radius: 6px;
}
input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 10px;
  height: 10px;
  /* background: #fff; Couleur du curseur */
  border-radius: 50%;
  margin-top: -3px; /* Ajustement de la position verticale */
}
.menu{
  cursor: pointer;
  /* position: relative; */
}
.menu img{
  width: 25px;
}
@media (max-width: 1600px) {
  .player-left{
    width: 30%;
  }
}
@media (max-width: 1200px) {
  .player-left{
    width: 40%;
  }
  .player-right .displayer, .player-right .volume {
    display: none;
  }
  
}
@media (max-width: 800px) {
  .player{
    height: 25px;
  }
  .player-center{
    position: relative;
    left: 4%;
    width: 75%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
  }
  .player-left{
    display: none;
  }
  .menu {
    position: absolute;
    right: 10%;
  }
  
}
