
.head{
    position: sticky;
    top: 0;
    margin: 0 auto;
    padding:  45px 0;
    width: 82%;
    z-index: 1;
}
.header{
    display: inline-flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 110px;
}
.header-left{
    width: 12em;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;
}
.header-left .img{
    min-width: 30%;
}
.img img{
    width: 100%;
}
.header-left p{
    color: #000;
    font-family: "Trajan Pro regular";
    font-size: 1.2em;
    letter-spacing: 1px;
    width: 90%;
}
.header-center{
    width: 20em;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.header-center img{
    width: 8px;
}
.header-center .p  {
    color: #000;
    font-family: "Inter Bold";
    font-size: 1.2em;
    text-decoration: none;
    margin-left: 20px;
}
.header-right{
    color: #000;
    font-family: 'ClashDisplay-semibold';
    font-size: 1.2em;
}
.content{
    padding-top:  10em;
    padding-bottom:  10em;
    margin: 0 auto;
    width: 83%;
}
.title-banner{
    position: relative;
    display: flex;
}
.title-banner .big-title{
    font-family: 'ClashDisplay-semibold';
    font-size: 2.5em;
    line-height: 100%;
}
  
.big-title #radioEmia{
    color: #FF7E67;
}

.title-banner .btn{
    position: absolute;
    right: 0;
    bottom: 40px;
    border-radius: 25px;
    border: 4px solid;
    display: flex;
    padding: 0.5% 4%;
    justify-content: center;
    align-items: center;
    font-family: "Inter bold";
    font-size: 100%;
    background-color: transparent;
    transition: all ease-in-out 100ms;
}
.title-banner .btn:hover{
    border: 4px solid #000 ;
    background-color: #000;
    color: #fff;
}
.paragraph-banner{
    display: flex;
    font-family:  "inter";
    font-size: 100%;
    font-weight: 500;
    position: relative;
}

.content-left-side{
    display: flex;
    width: 35%;
    flex-direction: column;
    align-items: flex-start;
    /* border: 1px solid; */
}
.content-left-side .paragraph {
    margin-top: 10px;
    margin-bottom: 40px;
}
.content-left-side h3{
    font-family: 'ClashDisplay-semibold';
    font-size: 100%;
}
.content-left-side ul{
    padding: 0;
    margin-top: -5px;
    list-style: none;
}
.content-left-side ul li{
    list-style: none;
    line-height: 15px;
}
.content-right-side{
    position: absolute;
    right: -10.25%;
    top: 12px;
    width: 69.5%;
    height: 100%;
    background: url("/public/images/people.jpeg") center;
    display: flex;
    justify-content: center;
    align-items: center;
}
.content-right-side #radioImage{
    width: 100%;
    position: absolute;
}

@media (max-width: 1600px) {
    .header{
        height: 110px;
    }
    .header-left{
        gap: 10px;
        width: 10em;
    }
    .header-left p{
        padding-left: 30px;
        font-size: 0.8em;
    }
    .header-center .p  {
        font-size: 70%;
    }
    .header-right{
        font-size: 0.8em;
    }
    .content{
        padding-top:  4.5em;
        padding-bottom: 0;
    }
    .title-banner .big-title{
        font-size: 1.5em;
    }
    .header-center img{
        width: 6px;
    }
    .paragraph-banner{
        font-size: small;
    }
    .content-right-side{
        top: -10px;
        height: 100%;
        background-size: cover;
    }
    .title-banner .btn{
        border: 3px solid;
        font-size: small;
    }
    .content-left-side ul li{
        line-height: 10px;
    }
    .content-left-side .paragraph {
        margin-bottom: 20px;
    }
}

@media (max-width: 1200px) {

    .head{
        position: relative;
        padding:  15px 0;
        width: 90%;
    }
    .header-left p{
        border: none;
    }
    .header-left .img{
        display: none;
    }
    .header-left{
        
        width: 8em;
    }
    .header-left p{
        padding-left: 5px;
    }
    .header-center{
        display: none;
    }
    .content{
        padding: 0;
        width: 90%;
        padding-top:  1em;
    }
    .title-banner .big-title{
        font-size: 1.2em;
    }
    .title-banner{
        display: block;
    }
    .title-banner .div-btn{
        margin-top: 8vh;
        margin-bottom: 1vh;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .title-banner .btn{
        position: relative;
        bottom: 0;
        font-size: 100%;
        border: 3px solid;
    }
    .title-banner .btn:hover{
        border: 3px solid #000 ;
    }
    .paragraph-banner{
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 10px;
    }
    .content-right-side{
        position: inherit;
        top: 20px;
        left: -5vw;
        width: 100vw;
        height: 200px;
    }
    .content-left-side{
        width: 90%;
        margin-top: 30px;
        font-size: 14px;
    }

} 
